body{
    background: black;
    padding:0 0;
    margin:0 0;
}

li{
    list-style: none;
    margin-top: 5%;
}

li,td{
    color:white;
}

.banner{
    width:100%;
    height:400px;
}

.banner >img{
    width:100%;
    height:100%;
    opacity: .6;
}


.pitch {
	margin: 0 3%;
	position: absolute;
	top: 15%;
	font-size: 1.8vw;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.pitch-title {
	font-size: 3em;
	margin: 0 0 0.2em;
	font-weight: 800;
    color:white;
}

.pitch-subtitle {
	margin: 0 0 0.5em;
    color:white;
    font-weight: 700;
}

.seats{
    box-sizing: border-box;
    width:100%;
    display: flex;
    flex-direction: row;
    gap: 1em;
    padding-left: 20em;
    background-color:#0E0F11;
}
.btn{
    background-color:#5c4848;
    width:1em;
    height:1.2em;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    cursor: pointer;
    outline:0;
    
}
.seat__selected{
    background-color: green;
}

.seat__occupied{
    background-color: red;
}

.results{
    display:flex;
    flex-direction: column;
    width:50%;
    margin:0 auto;
    box-sizing: border-box;
    padding:1em 0 0 10em;
    margin-bottom:3em;
}

.results > button{
    width:30%;    
    cursor:pointer;
    border:0;
    outline:0;
    margin-left:4em;
}

.select{
    width:65%;
    background-color: white;
  border: thin solid blue;
  border-radius: 4px;
  font: inherit;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
}


.seats-input{
    width:60%;
    margin-bottom: .2em;
    color:white;
    border:1px solid white;
    font-size: 1.4em;
    word-break: break-all;
    padding: 0 0 0 1em;
}
.total{
    margin-bottom: 1em;
    width:20%;
    color:white;
    border:1px solid white;
    font-size: 1.4em;
    padding: 0 0 0 1em;
}

/*button buy*/
.animate
{
	transition: all 0.1s;
	-webkit-transition: all 0.1s;
}

.action-button
{
	padding: 3px 3px;
     margin: 0px 10px 10px 0px;
	border-radius: 1px;
	font-family: 'Pacifico', cursive;
	font-size: 1.2em;
	color: #FFF;
	text-decoration: none;	
}

.blue
{
	background-color: #03a9f4;
	border-bottom: 5px solid #2980B9;
	text-shadow: 0px -2px #2980B9;
}
.action-button:active
{
	transform: translate(0px,5px);
  -webkit-transform: translate(0px,5px);
	border-bottom: 1px solid;
}

.movies{
    width:100%;
    display: grid;
    grid-template-columns: repeat(20,15%);
    overflow-y: hidden;
    overflow-x: scroll;
    background-color:#0E0F11;
    margin-bottom:3em;
}

.loading{
    text-align: center;
    color:white;
    font-size:1em;
}
.card {
    height: 14em;
    background: #fff;
    box-shadow: 0 3px 6px white, 0 3px 6px white;
    margin: 1em 0 3em 2em;
    transition: box-shadow .2s ease-in-out; 
    border:1px solid;
    border-radius: 5px 5px;
  }

  .card:hover{
    transform:translateX(5px);
    border:1px solid white;
    cursor: pointer;
  }

  .card > img{
    width:100%;
    height: 100%;
    border-radius: 5px 5px;
  }

  /*scrollbar*/
  ::-webkit-scrollbar {
    width: 7px;
    height: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }


  .hidden{
      display:none;
  }

  /*card de compras*/
  .card-shop{
      box-sizing: border-box;
    width: 30%;
    height: 220px;
    background: #FFFFFF;
    box-shadow: 0px 10px 25px -15px white;
    border-radius: 15px;
    margin:0 0 8em 30em;
    transition:300ms;
    cursor:pointer;   
    padding:1em 0 0 1em;
    overflow: hidden;
  }
  
  .card-shop:hover{
       box-shadow: 0px 30px 100px -5px rgba(35, 68, 126, 0.388877);
        transform:translateX(5px);
      }
    
    .top{
        display: flex;
        flex-direction: row;
        gap:1em;
      }
    .top>img{
        width:50px;
        height:50px;
        margin-top:.4em;
    }

    .top>p{
        margin:-.2em 0 0 6em;
        color:gray;
    }

    .bottom > p{
      color:black;
    }

    .title-movie{
      font-size:1.2em;
      font-weight: 700;
    }
    .money{
      font-size:1.4em;
      padding:0 0;
      margin:0 1em 0 0;
      width:fit-content;
      float: right;
    }

    @media screen and (max-width:1050px){
      .banner{
        height:300px;
      }
      .pitch{
        font-size:1.6em;
        top:3%;
      }

      /* movies**/
      .movies{
          grid-template-columns: repeat(20, 30%);
      }

      .card{
        height:12em;
      }

      /*seats */
      .seats li,.btn{
        width:10%;
      }
      .btn{
        width:60%;
      }
      .seats{
        flex-direction: column;
        padding-left:0;
        padding:.5em  0 0 .8em;
      }

      /*resultados compra*/
      .results{
        margin:1em  0 30em 0;
        padding:1em  0 0 10em;
        width:100%;
      }
      .select, .seats-input{
        width:50%;
      }
      .total{
        width:20%;
      }
      .results >button{
        width:50%;
        margin-left:1.5em;
      }

      /*after click comprar*/
      .card-shop{
        margin:0 0 30em 0;
        width:100%;
        height:250px;
        border-radius: 5px 0;
      }

      .top>p{
        margin-left:25em;
      }
      .title-movie{
        font-size:1.6em;
      }
      .money{
        font-size: 1.5em;
      }

    }

